"use strict";

var React = React;
var BrowserTreeNode = BrowserTreeNode;
var ctx = ctx;
var folderStore = folderStore;
var folderActions = folderActions;

var BrowserFolderTree = React.createClass({
	displayName: "BrowserFolderTree",
	getInitialState: function getInitialState() {
		return _createState();
	},
	componentDidMount: function componentDidMount() {
		folderStore.addListener(this.folderUpdated);
		folderActions.load();
	},
	componentWillUnmount: function componentWillUnmount() {
		folderStore.removeListener(this.folderUpdated);
	},
	folderUpdated: function folderUpdated() {
		this.setState(_createState());
	},
	render: function render() {

		var tree;

		if (this.state.folder) {
			tree = React.createElement(
				"ul",
				{ className: "tree-container" },
				React.createElement(BrowserTreeNode, { key: this.state.id,
					folder: this.state.folder,
					selectedId: this.state.selectedId })
			);
		}

		return React.createElement(
			"div",
			{ className: "browser-folders tree" },
			tree
		);
	}
});

function _createState() {
	return {
		folder: folderStore.get(),
		selectedId: folderStore.getSelectedId()
	};
}